<template>
  <div
      :class="{'login-container height-greater-800':height > 800 && isPc, 'login-container': height <= 800 && isPc, 'login-container-mobile' : !isPc}">
    <div class="card-box" >
      <span class="login-title">{{ $t('Login.Title') }}</span>
      <div class="item-box">
        <span class="address-span email-span">{{ $t('Login.Text-1') }}</span>
        <div class="tips-span" style="margin-bottom: 38px;">{{ $t('Login.Text-2') }}</div>
        <div class="login-email-box">
          <a-input class="email-input" :placeholder="$t('Login.Text-3')" v-model="email"
                   @change="clearTipsMsg"></a-input>
          <!--   <span class="send-span" @click="sendEmail">{{ sendMsg }}</span>-->
        </div>
        <div class="login-email-box" style="margin-top: 30px">
          <a-input class="email-input" :placeholder="$t('Login.Text-4')" v-model="password" type="password"
                   @change="clearTipsMsg"></a-input>
          <!--     <span class="send-span" @click="sendEmail">{{ sendMsg }}</span>-->
        </div>
        <span class="email-message" :class="{'email-unbind': !sendResult }" v-if="isSend">
                  {{ sendResult ? '邮箱已发送，请注意查收！ ' : '邮箱未绑定' }}
                </span>
        <span class="email-message email-unbind" v-if="tipsMsg.length > 0">
                  {{ tipsMsg }}</span>
        <button type="button" class="submit-btn ant-btn ant-btn-primary login" @click="login">
          <span>{{ $t('Login.Button-1') }}</span>
        </button>
      </div>
      <p class="bottom-span">{{ $t('Login.Text-5') }} <a @click="openZw">{{ $t('order/confirm.Paragraph-2-2') }}</a></p>
    </div>
  </div>
</template>

<script>
import animationData from "../assets/lottie/au.json";
import {AU_EMAIL, AU_HMAC, AU_UUID} from "@/store/Global";

export default {
  name: 'Login',
  data() {
    return {
      address: '',
      email: '',
      defaultOptions: {
        animationData: animationData,
      },
      animationSpeed: 6,
      anim: {},
      sendMsg: '发送',
      sendResult: false,
      tipsMsg: '',
      password: '',
      isSend: false,
      height: this.$height(),
      countTime: '',
      secondNum: 60,
      isPc: this.isPc(),
    };
  },
  destroyed() {
    window.clearInterval(this.countTime);
  },
  mounted() {
    if (this.$route.query.email) {
      this.email = this.$route.query.email
    }
  },
  methods: {
    login() {
      let that = this;
      const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
      if (!emailReg.test(that.email) || !that.email) {
        that.tipsMsg = '请输入合法的邮箱';
      }
      if (!that.password) {
        that.tipsMsg = '请输入密码';
      }
      that.request.login({
        email: that.email,
        password: that.password
      }).then((res) => {
        if (res.code == 1) {
          localStorage.setItem(AU_UUID, res.data.user.uuid)
          localStorage.setItem(AU_HMAC, res.data.access_token)
          localStorage.setItem(AU_EMAIL, res.data.user.account);
          this.$store.commit('setToken', res.data.access_token)
          window.location.href = "/#/user/service"
        } else {
          that.tipsMsg = res.msg;
        }
      });
    },
    clearTipsMsg() {
      this.tipsMsg = '';
      this.isSend = false;
    },
    countDown() {
      this.countTime = setInterval(() => {
        this.sendMsg = '(' + this.secondNum + ')';
        this.secondNum--;
        if (this.secondNum === 0) {
          this.secondNum = 60;
          window.clearInterval(this.countTime);
          this.sendMsg = '重发';
        }
      }, 1000);
    },
    sendEmail() {
      if (this.sendMsg === '发送' || this.sendMsg === '重发') {
        this.isSend = false;
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        if (this.email) {
          if (emailReg.test(this.email)) {
            this.countDown();
            const that = this;
            this.$request.auLoginWithEmail(this.email, function (data) {
              that.isSend = true;
              //that.sendMsg = '重发';
              if (data && (data.status === 200 || data.status === 201) && data.data) {
                that.sendResult = data.data.result === "succeed";
                if (!that.sendResult) {
                  window.clearInterval(that.countTime);
                  that.sendMsg = '重发';
                }
              } else {
                window.clearInterval(that.countTime);
                that.sendMsg = '重发';
                that.sendResult = false;
              }
            })
          } else {
            this.tipsMsg = '请输入合法的邮箱';
          }
        } else {
          this.tipsMsg = '请输入合法的邮箱';
        }
      }
    },
    handleAnimation: function (anim) {
      this.anim = anim;
      // console.log(anim); //这里可以看到 lottie 对象的全部属性
    },
    openZw() {

    },
  }
};
</script>

